.page {
  height: 100vh; }

body {
  background-color: #11171d; }

.custom-container {
  height: 100%;
  margin-right: 0px !important;
  margin-left: 0px !important; }
  @media (min-width: 992px) {
    .custom-container {
      display: flex; } }

a {
  color: #e85223;
  font-size: 12pt;
  font-family: 'Avenir'; }
  @media (min-width: 992px) {
    a {
      letter-spacing: 0.5pt; } }

p {
  color: #bdccd4;
  font-size: 12pt;
  font-weight: 100;
  font-family: 'Avenir'; }
